.shop-banners {
  padding-top: 45px;

  // .shop-banners__container
  &__container {
    display: flex;
    justify-content: space-between;
    padding: 0;

    @include xs {
      flex-direction: column;
    }
  }
}

.shop-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
  width: 50%;
  min-height: 420px;
  color: var(--color-white);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-dark);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(215, 8, 8, 0.1);
  }

  // .shop-banner__title
  &__title {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 48px;

    @include md {
      margin-bottom: 10px;
      font-size: 35px;
    }

    @include mobile {
      font-size: 27px;
    }
  }

  @include md {
    padding: 30px;
    min-height: 300px;
  }

  @include xs {
    width: 100%;
  }
}

.shop-banner--men {
  background-image: url("../img/shop-banner/01.jpg");
}

.shop-banner--woomen {
  background-image: url("../img/shop-banner/02.jpg");
}
