.breadcrumbs {
  // .breadcrumbs__list
  &__list {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #eee;
    padding-top: 36px;
    padding-bottom: 26px;

    @include mobile {
      padding: 20px 0;
    }
  }

  // .breadcrumbs__item
  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 34px;

      &::after {
        content: "/";
        position: absolute;
        right: -22px;
        top: 50%;
        font-size: 10px;
        color: var(--color-gray);
        transform: translateY(-50%);
      }
    }
  }

  // .breadcrumbs__link
  &__link {
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    transition: ease color 0.3s;

    @media (min-width: 992px) {
      &:hover {
        color: var(--color-dark);
        transition: ease color 0.3s;
      }
    }
  }

  // .breadcrumbs__link--current
  &__link--current {
    color: var(--color-dark);
  }
}

.breadcrumbs .card-prev,
.breadcrumbs .card-next {
  display: none;
}

.card {
  .breadcrumbs .card-prev,
  .breadcrumbs .card-next {
    display: inline-block;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    transition: ease color 0.3s;

    @media (min-width: 992px) {
      &:hover {
        color: var(--color-dark);
        transition: ease color 0.3s;
      }
    }
  }

  .breadcrumbs .container {
    display: flex;
    align-items: center;
  }

  .card-prev {
    margin-left: auto;
  }

  .card-next {
    margin-left: 20px;
  }

  .breadcrumbs__list {
    border-bottom: none;
  }
}
