.card-slider {
  display: flex;

  // .card-slider__thumbs
  &__thumbs {

    @include xs {
      display: flex;
      flex-wrap: wrap;
      margin-top: 20px;
    }
  }

  // .card-slider__thumb
  &__thumb {
    cursor: pointer;

    img {
      pointer-events: none;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    @include xs {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }

  // .card-slider__main
  &__main {
    margin-left: 14px;
    width: 686px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include xs {
      margin-left: 0;
      width: 100%;
    }
  }

  @include xs {
    flex-direction: column-reverse;
  }
}
