html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 15px;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

button {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100%;
}

.page {
  flex: 1 1 auto;
}

.stop-scroll {
  @include stop-scroll;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
  @include visually-hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 48px;
  max-width: 1920px;

  @include xs {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  display: inline-block;
  padding-bottom: 8px;
  color: var(--color-dark);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    overflow: hidden;
    width: 0;
    height: 2px;
    background-color: currentColor;
    transition: width 0.3s ease;
  }

  &--white {
    color: var(--color-white);
  }

  @media (min-width: 992px) {
    &:hover {
      &::before {
        width: 100%;
        transition: width 0.3s ease;
      }
    }
  }
}

.container-narrow {
  max-width: 1421px;
}

.qualitty {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  padding: 0 5px;
  height: 17px;
  font-size: 10px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;

  // .custom-checkbox__input
  &__input {
    &:checked + span {
      color: var(--color-dark);

      &::before {
        border-color: var(--color-dark);
      }

      &::after {
        color: var(--color-dark);
      }
    }
  }

  // .custom-checkbox__text
  &__text {
    position: relative;
    padding-left: 15px;
    font-size: 13px;
    color: var(--color-gray);
    transition: ease color 0.3s;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 3px;
      border: 2px solid #aaa;
      width: 11px;
      height: 11px;
    }

    &::after {
      content: "\2713";
      position: absolute;
      left: 1px;
      top: 2px;
      width: 11px;
      height: 11px;
    }

    @media (min-width: 992px) {
      &:hover {
        color: var(--color-dark);
        transition: ease color 0.3s;
      }
    }
  }
}

.custom-select {
  position: relative;
  min-width: 115px;

  // .custom-select__top
  &__top {
    position: relative;
    border-bottom: 1px solid #e5e5e7;
    padding-right: 32px;
    padding-bottom: 13px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    cursor: pointer;
    user-select: none;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      width: 10px;
      height: 10px;
      background-image: url("../img/sprite.svg#down-arrow");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      transition: ease transform 0.3s;
    }
  }

  // .custom-select__dropdown
  &__dropdown {
    position: absolute;
    left: 0;
    top: calc(100% - 2px);
    z-index: 20;
    padding: 10px 8px;
    width: 100%;
    background-color: #f5f5f5;
    opacity: 0;
    visibility: hidden;
    transition: ease opacity 0.3s, visibility 0.3s;
  }

  // .custom-select__item
  &__item {
    transition: ease color 0.3s;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 11px;
    }

    @media (min-width: 992px) {
      &:hover {
        color: var(--color-gray);
        transition: ease color 0.3s;
      }
    }
  }

  &--open {
    .custom-select__top {
      &::after {
        transform: rotate(-180deg);
        transition: ease transform 0.3s;
      }
    }

    .custom-select__dropdown {
      opacity: 1;
      visibility: visible;
      transition: ease opacity 0.3s, visibility 0.3s;
    }
  }

  &--footer {
    min-width: auto;

    .custom-select__top {
      border-bottom: none;
      padding-right: 13px;
      padding-bottom: 0;
      font-size: 13px;

      &::after {
        top: 1px;
      }
    }
  }
}

.info-price {
  display: flex;
  align-items: flex-end;

  // .info-price__current
  &__current {
    font-weight: 700;
    font-size: 13px;
  }

  // .info-price__old
  &__old {
    padding-left: 4px;
    font-weight: 500;
    font-size: 13px;
    text-decoration: line-through;
    color: #959595;
  }
}

.product-prop {
  display: inline-flex;
  padding-left: 20px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-dark);
}

.to-top {
  position: fixed;
  right: 2%;
  bottom: 5%;
  z-index: 15;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);

  svg {
    width: 17px;
    height: 18px;
    transition: ease transform 0.3s;
  }

  @media (min-width: 992px) {
    &:hover {
      svg {
        transform: translateY(-2px);
        transition: ease transform 0.3s;
      }
    }
  }
}