html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 15px;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

button {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100%;
}

.page {
  flex: 1 1 auto;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 768px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 48px;
  max-width: 1920px;
}

@media (max-width: 576px) {
  .container {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  display: inline-block;
  padding-bottom: 8px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  overflow: hidden;
  width: 0;
  height: 2px;
  background-color: currentColor;
  transition: width 0.3s ease;
}

.main-link--white {
  color: var(--color-white);
}

@media (min-width: 992px) {
  .main-link:hover::before {
    width: 100%;
    transition: width 0.3s ease;
  }
}

.container-narrow {
  max-width: 1421px;
}

.qualitty {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  padding: 0 5px;
  height: 17px;
  font-size: 10px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;
}

.custom-checkbox__input:checked + span {
  color: var(--color-dark);
}

.custom-checkbox__input:checked + span::before {
  border-color: var(--color-dark);
}

.custom-checkbox__input:checked + span::after {
  color: var(--color-dark);
}

.custom-checkbox__text {
  position: relative;
  padding-left: 15px;
  font-size: 13px;
  color: var(--color-gray);
  transition: ease color 0.3s;
}

.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  border: 2px solid #aaa;
  width: 11px;
  height: 11px;
}

.custom-checkbox__text::after {
  content: "\2713";
  position: absolute;
  left: 1px;
  top: 2px;
  width: 11px;
  height: 11px;
}

@media (min-width: 992px) {
  .custom-checkbox__text:hover {
    color: var(--color-dark);
    transition: ease color 0.3s;
  }
}

.custom-select {
  position: relative;
  min-width: 115px;
}

.custom-select__top {
  position: relative;
  border-bottom: 1px solid #e5e5e7;
  padding-right: 32px;
  padding-bottom: 13px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  cursor: pointer;
  user-select: none;
}

.custom-select__top::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 10px;
  height: 10px;
  background-image: url("../img/sprite.svg#down-arrow");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: ease transform 0.3s;
}

.custom-select__dropdown {
  position: absolute;
  left: 0;
  top: calc(100% - 2px);
  z-index: 20;
  padding: 10px 8px;
  width: 100%;
  background-color: #f5f5f5;
  opacity: 0;
  visibility: hidden;
  transition: ease opacity 0.3s, visibility 0.3s;
}

.custom-select__item {
  transition: ease color 0.3s;
  cursor: pointer;
}

.custom-select__item:not(:last-child) {
  margin-bottom: 11px;
}

@media (min-width: 992px) {
  .custom-select__item:hover {
    color: var(--color-gray);
    transition: ease color 0.3s;
  }
}

.custom-select--open .custom-select__top::after {
  transform: rotate(-180deg);
  transition: ease transform 0.3s;
}

.custom-select--open .custom-select__dropdown {
  opacity: 1;
  visibility: visible;
  transition: ease opacity 0.3s, visibility 0.3s;
}

.custom-select--footer {
  min-width: auto;
}

.custom-select--footer .custom-select__top {
  border-bottom: none;
  padding-right: 13px;
  padding-bottom: 0;
  font-size: 13px;
}

.custom-select--footer .custom-select__top::after {
  top: 1px;
}

.info-price {
  display: flex;
  align-items: flex-end;
}

.info-price__current {
  font-weight: 700;
  font-size: 13px;
}

.info-price__old {
  padding-left: 4px;
  font-weight: 500;
  font-size: 13px;
  text-decoration: line-through;
  color: #959595;
}

.product-prop {
  display: inline-flex;
  padding-left: 20px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-dark);
}

.to-top {
  position: fixed;
  right: 2%;
  bottom: 5%;
  z-index: 15;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
}

.to-top svg {
  width: 17px;
  height: 18px;
  transition: ease transform 0.3s;
}

@media (min-width: 992px) {
  .to-top:hover svg {
    transform: translateY(-2px);
    transition: ease transform 0.3s;
  }
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 12;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__logo {
  display: inline-block;
  margin-right: 13.28125%;
}

@media (max-width: 1200px) {
  .header__logo {
    margin-right: 0;
  }
}

@media (max-width: 576px) {
  .header__logo {
    width: 90px;
    height: 18px;
  }
}

.nav {
  display: block;
}

.nav__list {
  display: flex;
}

.nav__list li {
  margin-right: 45px;
}

.nav__list li:last-child {
  margin-right: 0;
}

@media (max-width: 1200px) {
  .nav__list {
    display: block;
  }
  .nav__list li {
    margin-right: 0;
  }
}

.nav__link--current::before {
  width: 100%;
  transition: width 0.3s ease;
}

@media (max-width: 1200px) {
  .nav__link--current::before {
    width: 0;
    transition: none;
  }
}

@media (max-width: 1200px) {
  .nav__link {
    display: inline-block;
    border-bottom: 1px solid var(--color-dark);
    padding: 15px 10%;
    width: 100%;
    font-size: 22px;
  }
}

.nav__close {
  position: absolute;
  right: 10%;
  top: 5%;
  display: none;
}

@media (max-width: 1200px) {
  .nav__close {
    display: block;
  }
}

@media (max-width: 1200px) {
  .nav {
    position: fixed;
    left: -100%;
    top: 0;
    padding: 10% 0;
    width: 80%;
    height: 100vh;
    background-color: var(--color-white);
    transition: ease-in-out left 0.3s;
  }
}

.nav--visible {
  left: 0;
  transition: ease-in-out left 0.3s;
}

.shop-nav {
  display: inline-flex;
  align-items: flex-start;
}

.shop-nav li {
  margin-right: 25px;
}

.shop-nav li:last-child {
  margin-right: 0;
}

@media (max-width: 576px) {
  .shop-nav li {
    margin-right: 15px;
  }
}

.shop-nav__link {
  display: inline-flex;
  background-position: center;
  background-repeat: no-repeat;
  transition: ease opacity 0.3s;
}

@media (min-width: 992px) {
  .shop-nav__link:hover {
    opacity: 0.3;
    transition: ease opacity 0.3s;
  }
}

.shop-nav__link--user {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/first-slider/i-2.png");
}

.search-form__btn {
  width: 16px;
  height: 16px;
  background-image: url("../img/first-slider/i-1.png");
}

.cart__icon {
  display: inline-block;
  flex: 0 0 16px;
  margin-right: 6px;
  width: 16px;
  height: 16px;
  background-image: url("../img/first-slider/kz-bag.png");
}

.catalog-header {
  padding-top: 40px;
}

.catalog-header__logo {
  margin-right: 18.75%;
}

@media (max-width: 768px) {
  .catalog-header__logo {
    margin-right: 0;
  }
}

@media (max-width: 768px) {
  .catalog-header {
    padding-top: 0;
  }
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: #111;
  /* .free-delivery__btn */
}

.free-delivery__btn {
  position: absolute;
  right: 40px;
  top: 50%;
  z-index: 14;
  width: 12px;
  height: 12px;
  transform: translateY(-50%);
}

.free-delivery__btn svg {
  z-index: 20;
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-white);
}

@media (max-width: 768px) {
  .free-delivery {
    display: none;
  }
}

.card-page .catalog-header {
  padding-top: 0;
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 18px;
  color: var(--color-dark);
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
  transform: translateY(-50%);
}

@media (max-width: 1200px) {
  .burger {
    display: block;
  }
  .burger::before, .burger::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    background-color: var(--color-dark);
  }
  .burger::before {
    top: 0;
    height: 2px;
  }
  .burger::after {
    top: auto;
    bottom: 0;
    height: 2px;
  }
}

.hero {
  position: relative;
  z-index: 2;
  display: flex;
  padding-top: 122px;
  min-height: 100vh;
  background: var(--quill-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.05);
}

.hero__container {
  margin: 0 auto;
  padding: 0 50px;
  max-width: 1424px;
}

@media (max-width: 576px) {
  .hero__container {
    padding: 0 20px;
  }
}

.hero .hero__content {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.hero__text {
  margin-bottom: 130px;
  max-width: 50%;
  word-break: break-word;
}

@media (max-width: 768px) {
  .hero__text {
    max-width: 100%;
  }
}

.hero__title {
  margin-bottom: 26px;
}

.text-hero__title {
  font-weight: 700;
  font-size: 72px;
  line-height: calc(86 / 72);
  color: var(--color-dark);
}

@media (max-width: 1200px) {
  .text-hero__title {
    font-size: 50px;
  }
}

@media (max-width: 1200px) {
  .text-hero__title {
    font-size: 50px;
  }
}

@media (max-width: 768px) {
  .text-hero__title {
    font-size: 35px;
  }
}

.text-hero__descr {
  font-weight: 500;
  font-size: 18px;
  color: var(--color-dark);
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  width: 100% !important;
  height: 100%;
  background-image: url("../img/first-slider/bg.png");
  background-position: 99% 100%;
  background-size: auto 100%;
  background-repeat: no-repeat;
}

@media (max-width: 768px) {
  .banner-slider .swiper-slide {
    background-image: none;
  }
}

.banner-pag,
.hero-pag {
  left: 50%;
  bottom: 46px;
  transform: translateX(-50%);
}

.banner-pag .swiper-pagination-bullet,
.hero-pag .swiper-pagination-bullet {
  margin: 0 12px;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active,
.hero-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::after,
.hero-pag .swiper-pagination-bullet-active::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: #fff;
