.footer {
  // .footer__content
  &__content {
    padding: 70px 250px;
    padding-bottom: 0;

    @include lg {
      padding: 0 100px;
      padding-top: 60px;
    }

    @include md {
      padding: 0;
      padding-top: 40px;
    }
  }

  // .footer__top
  &__top {
    display: flex;
    justify-content: space-between;
    padding-bottom: 70px;

    @include md {
      flex-wrap: wrap;
      padding-bottom: 40px;
    }
  }

  // .footer__column
  &__column {
    &:nth-child(2) {
      margin-left: -45px;

      @include md {
        margin-left: 0;
      }
    }

    &:nth-child(3) {
      @include md {
        margin-top: 30px;
        width: 100%;
      }
    }

    &--flex {
      display: flex;
      align-items: center;
    }
  }

  // .footer__title
  &__title {
    margin-bottom: 38px;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
    color: var(--color-dark);
  }

  // .footer__item
  &__item {
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }

  // .footer__link
  &__link {
    position: relative;
    padding-bottom: 8px;
    font-size: 13px;
    color: var(--color-tundora);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      overflow: hidden;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      opacity: 0;
      transition: ease opacity 0.3s;
    }

    @media (min-width: 992px) {
      &:hover {
        &::before {
          opacity: 1;
          transition: opacity 0.3s ease;
        }
      }
    }
  }

  // .footer__form
  &__form {
    width: 647px;
    transform: translateY(3px);

    @include lg {
      width: 500px;
    }

    @include md {
      width: 100%;
    }
  }

  // .footer__text
  &__text {
    font-size: 13px;
    color: var(--color-tundora);
  }

  // .footer__bottom
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #eee;
    padding: 65px 0;
    padding-bottom: 30px;

    @include md {
      padding-top: 40px;
    }

    @include xs {
      flex-direction: column;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  // .footer__list--row
  &__list--row {
    display: flex;

    .footer__item--row {
      margin-bottom: 0;

      &:not(:last-child) {
        margin-right: 44px;

        @include mobile {
          margin-right: 20px;
        }

        @include xs {
          margin-right: 13px;
        }
      }
    }
  }

  // .footer__link--row
  &__link--row {
    font-weight: 700;
    font-size: 13px;
    color: var(--color-dark);

    @include xs {
      font-size: 12px;
    }
  }

  &__select {
    display: flex;
    align-items: center;

    &:not(:first-child) {
      margin-left: 60px;

      @include md {
        margin-left: 30px;
      }
    }

    &-caption {
      margin-right: 14px;
      font-size: 13px;
      color: var(--color-gray);
    }
  }

  // .footer__right
  &__right {
    display: flex;
    align-items: center;

    @include xs {
      margin-top: 10px;
    }
  }

  &__copy {
    font-size: 13px;
  }
}

.subscribe-form {
  // .subscribe-form__title
  &__title {
    margin-bottom: 38px;
    font-weight: 500;
    font-size: 13px;
    color: var(--color-tundora);
  }

  // .subscribe-form__field
  &__field {
    position: relative;
    display: block;
    margin-bottom: 26px;
    border-bottom: 2px solid var(--color-dark);
    padding-bottom: 8px;
    width: 100%;

    input {
      border: none;
      padding-right: 80px;
      width: 100%;
      color: var(--color-dark);

      ::placeholder {
        font-family: "Jost", sans-serif;
        font-weight: 500;
        font-size: 13px;
        color: rgba(69, 69, 69, 0.5);
      }
    }
  }

  // .subscribe-form__btn
  &__btn {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;
    padding: 13px 10px;
    font-weight: 500;
    font-size: 13px;
    color: var(--color-dark);
  }
}

.social {
  display: flex;
  align-items: center;

  // .social__item
  &__item {
    &:not(:last-child) {
      margin-right: 34px;

      @include mobile {
        margin-right: 28px;
      }
    }
  }

  // .social__link
  &__link {
    display: block;
    width: 16px;
    height: 16px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: ease opacity 0.3s;

    &:hover {
      opacity: 0.7;
      transition: ease opacity 0.3s;
    }
  }

  // .social__link--tw
  &__link--tw {
    background-image: url("../img/footer/01.png");
  }

  // .social__link--fb
  &__link--fb {
    background-image: url("../img/footer/02.png");
  }

  // .social__link--in
  &__link--in {
    background-image: url("../img/footer/03.png");
  }

  // .social__link--pin
  &__link--pin {
    background-image: url("../img/footer/04.png");
  }
}

.footer--2 {
  .footer__top {
    align-items: center;
    border-top: 1px solid #eee;
    padding: 50px 0;

    @include mobile {
      flex-direction: column;
      align-items: center;
      padding: 25px 0 40px;
    }

    @include xs {
      padding: 20px 0 30px;
    }
  }

  .footer__bottom {
    border-top: none;
    padding: 0;
    padding-bottom: 10px;
  }

  .footer__column {
    &:not(:first-child) {
      @include mobile {
        margin-top: 20px;
      }
    }

    &:nth-child(2) {
      margin-left: 130px / 1920px * 100%;

      @include md {
        margin-left: 0;
      }
    }

    &:nth-child(3) {
      @include mobile {
        width: auto;
      }
    }
  }

  &:not(:first-child) {
    @include mobile {
      margin-top: 20px;
    }
  }
}

// card-page footer style
.card-page {
  .footer__content {
    padding-top: 102px;

    @include mobile {
      padding-top: 30px;
    }
  }

  .social {
    .social__item:not(:last-child) {
      margin-right: 28px;
    }
  }

  .footer__list {
    margin-left: 58px;

    @include mobile {
      margin-left: 0;
    }
  }
}
