.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 12;
  width: 100%;

  // .header__container
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 36px;
    padding-bottom: 36px;
  }

  // .header__logo
  &__logo {
    display: inline-block;
    margin-right: 255px / 1920px * 100%;

    @include md {
      margin-right: 0;
    }

    @include xs {
      width: 90px;
      height: 18px;
    }
  }
}

.nav {
  display: block;

  // .nav__list
  &__list {
    display: flex;

    li {
      margin-right: 45px;

      &:last-child {
        margin-right: 0;
      }
    }

    @include md {
      display: block;

      li {
        margin-right: 0;
      }
    }
  }

  // .nav__link--current
  &__link--current {
    &::before {
      width: 100%;
      transition: width 0.3s ease;
    }

    @include md {
      &::before {
        width: 0;
        transition: none;
      }
    }
  }

  &__link {
    @include md {
      display: inline-block;
      border-bottom: 1px solid var(--color-dark);
      padding: 15px 10%;
      width: 100%;
      font-size: 22px;
    }
  }

  &__close {
    position: absolute;
    right: 10%;
    top: 5%;
    display: none;

    @include md {
      display: block;
    }
  }

  @include md {
    position: fixed;
    left: -100%;
    top: 0;
    padding: 10% 0;
    width: 80%;
    height: 100vh;
    background-color: var(--color-white);
    transition: ease-in-out left 0.3s;
  }

  &--visible {
    left: 0;
    transition: ease-in-out left 0.3s;
  }
}

.shop-nav {
  display: inline-flex;
  align-items: flex-start;

  li {
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
    }

    @include xs {
      margin-right: 15px;
    }
  }

  // .shop-nav__link
  &__link {
    display: inline-flex;
    background-position: center;
    background-repeat: no-repeat;
    transition: ease opacity 0.3s;

    @media (min-width: 992px) {
      &:hover {
        opacity: 0.3;
        transition: ease opacity 0.3s;
      }
    }
  }

  // .shop-nav__link--user
  &__link--user {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url("../img/first-slider/i-2.png");
  }
}

.search-form {
  // .search-form__btn
  &__btn {
    width: 16px;
    height: 16px;
    background-image: url("../img/first-slider/i-1.png");
  }
}

.cart {
  // .cart__icon
  &__icon {
    display: inline-block;
    flex: 0 0 16px;
    margin-right: 6px;
    width: 16px;
    height: 16px;
    background-image: url("../img/first-slider/kz-bag.png");
  }
}

.catalog-header {
  padding-top: 40px;

  &__logo {
    margin-right: 360px / 1920px * 100%;

    @include mobile {
      margin-right: 0;
    }
  }

  @include mobile {
    padding-top: 0;
  }
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: #111;

  /* .free-delivery__btn */
  &__btn {
    position: absolute;
    right: 40px;
    top: 50%;
    z-index: 14;
    width: 12px;
    height: 12px;
    transform: translateY(-50%);

    svg {
      z-index: 20;
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--color-white);
    }
  }

  @include mobile {
    display: none;
  }
}

.card-page {
  .catalog-header {
    padding-top: 0;
  }
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 18px;
  color: var(--color-dark);

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 2px;
    background-color: var(--color-dark);
    transform: translateY(-50%);
  }

  @include md {
    display: block;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      background-color: var(--color-dark);
    }

    &::before {
      top: 0;
      height: 2px;
    }

    &::after {
      top: auto;
      bottom: 0;
      height: 2px;
    }
  }
}
