.hero {
  position: relative;
  z-index: 2;
  display: flex;
  padding-top: 122px;
  min-height: 100vh;
  background: var(--quill-gray);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0.05);
  }

  // .hero__container
  &__container {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1424px;

    @include xs {
      padding: 0 20px;
    }
  }

  .hero__content {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }

  // .hero__text
  &__text {
    margin-bottom: 130px;
    max-width: 50%;
    word-break: break-word;

    @include mobile {
      max-width: 100%;
    }
  }

  // .hero__title
  &__title {
    margin-bottom: 26px;
  }
}

.text-hero {
  // .text-hero__title
  &__title {
    font-weight: 700;
    font-size: 72px;
    line-height: calc(86 / 72);
    color: var(--color-dark);

    @include md {
      font-size: 50px;
    }

    @include md {
      font-size: 50px;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  // .text-hero__descr
  &__descr {
    font-weight: 500;
    font-size: 18px;
    color: var(--color-dark);
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;

  .swiper-slide {
    width: 100% !important;
    height: 100%;
    background-image: url("../img/first-slider/bg.png");
    background-position: 99% 100%;
    background-size: auto 100%;
    background-repeat: no-repeat;

    @include mobile {
      background-image: none;
    }
  }
}

.banner-pag,
.hero-pag {
  left: 50%;
  bottom: 46px;
  transform: translateX(-50%);

  .swiper-pagination-bullet {
    margin: 0 12px;
    width: 6px;
    height: 6px;
    background-color: var(--color-white);
    opacity: 0.5;
  }

  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--color-dark);
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -1;
      border-radius: 100%;
      width: 20px;
      height: 20px;
      background-color: #fff;
      transform: translate(-50%, -50%);
    }
  }
}

.hero-pag .swiper-pagination-bullet {
  background-color: var(--color-gray);
}

.hero-pag .swiper-pagination-bullet-active {
  background-color: var(--color-dark);
}

.marketing {
  position: absolute;
  left: 50px;
  bottom: 50px;
  z-index: 2;
  display: flex;
  padding: 11px;
  width: 280px;
  box-shadow: 0 5px 30px 0 rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vw);
  transition: transform 0.3s ease;

  // .marketing__image
  &__image {
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .marketing__text
  &__text {
    margin-left: 10px;
  }

  // .marketing__descr
  &__descr {
    display: block;
    margin-bottom: 8px;
    font-size: 11px;
    color: var(--color-gray);
  }

  // .marketing__title
  &__title {
    margin-bottom: 7px;
    padding-right: 10px;
    min-height: 34px;
    font-size: 15px;
    color: var(--color-dark);
  }

  // .marketing__when-from
  &__when-from {
    display: block;
    padding-right: 20px;
    font-size: 11px;
    color: var(--color-gray);
  }

  // .marketing__close
  &__close {
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 0;
    width: 10px;
    height: 10px;
    background-image: url("../img/modal/modal-i-2.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &--visible {
    transform: translateX(0);
    transition: transform 0.3s ease;
  }

  @include xs {
    left: 15px;
  }
}
