.products-grid {
  display: flex;
  flex-wrap: wrap;

  // .products-grid__item
  &__item {
    margin-right: 1.8%;
    margin-bottom: 80px;
    width: 18.5%;

    &:nth-last-child(-n+5) {
      margin-bottom: 0;

      @include md {
        margin-bottom: 4%;
      }
    }

    &:nth-child(5n) {
      margin-right: 0;

      @include md {
        margin-right: 5%;
      }

      @include xs {
        margin-right: 4%;
      }
    }

    &:nth-child(3n) {
      @include md {
        margin-right: 0 !important;
      }

      @include xs {
        margin-right: 4% !important;
      }
    }

    &:nth-child(2n) {
      @include md {
        margin-right: 5%;
      }

      @include xs {
        margin-right: 0 !important;
      }
    }

    @include md {
      margin-right: 5%;
      width: 30%;
    }

    @include xs {
      margin-right: 4%;
      width: 48%;
    }
  }
}

.product {
  position: relative;

  // .product__img
  &__img {
    position: relative;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    margin-bottom: 15px;

    img {
      width: 100%;
      max-height: 100%;
      object-fit: cover;
      transition: ease transform 0.3s;
    }
  }

  &__prop {
    margin-bottom: 8px;
  }

  // .product__title
  &__title {
    margin-bottom: 8px;
    font-size: 13px;
    color: var(--color-tundora);

    a {
      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
      }
    }
  }

  &__old-price {
    margin-left: 3px;
  }

  &:hover {
    .product__img {
      img {
        transform: scale(1.1);
        transition: ease transform 0.3s;
      }
    }

    .product-custom__price,
    .product-custom__cart {
      transform: translateY(-100%);
      transition: ease transform 0.3s;
    }
  }
}

.product-custom {
  overflow: hidden;
  height: 14px;

  // .product-custom__price
  &__price {
    transition: ease transform 0.3s;
  }

  .to-cart {
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
    color: var(--color-dark);
  }

  // .product-custom__cart
  &__cart {
    transition: ease transform 0.3s;
  }
}

