.sale {
  position: absolute;
  left: 20px;
  top: 20px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-dark);
  padding: 0 13px;
  height: 24px;
  font-weight: 900;
  font-size: 10px;
  text-transform: uppercase;

  @include xs {
    left: 10px;
    top: 10px;
  }
}

.old-price {
  font-weight: 500;
  font-size: 13px;
  text-decoration: line-through;
  color: #959595;
}
