.banners {
  &__container {
    padding-top: 50px;
    padding-bottom: 50px;

    @include md {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    @include xs {
      padding-bottom: 0;
    }
  }

  &__list {
    display: flex;

    @include md {
      flex-wrap: wrap;
    }
  }

  &__item {
    flex-shrink: 0;
    width: 33%;

    &:not(:last-child) {
      margin-right: 0.6%;
    }

    @include md {
      margin-right: 0;
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__article {
    padding: 52px 43% 52px 60px;
    min-height: 290px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #d7d7d7;

    @include md {
      padding: 45px 112px 20px 20px;
      background-size: cover;
    }

    @include mobile {
      padding-right: 65px;
    }

    @include xs {
      min-height: 210px;
    }
  }
}

.banners-article {
  // .banners-article__title
  &__title {
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 36px;

    @media (max-width: 1440px) {
      font-size: 30px;
    }
  }

  &--midseason {
    background-image: url("../img/first-slider/01.jpg");
  }

  &--summer {
    background-image: url("../img/first-slider/02.jpg");
  }

  &--acces {
    background-image: url("../img/first-slider/03.jpg");
  }
}
