.catalog-filters {
  margin-right: 30px;
  padding-top: 32px;
  padding-right: 62px;
  width: 309px;

  @include md {
    padding-right: 30px;
    width: 50%;
  }

  @include mobile {
    position: absolute;
    left: 0;
    top: 88px;
    z-index: 10;
    display: none;
    padding: 0 48px;
    width: 100%;
    background-color: #fff;
  }

  @include xs {
    padding: 0 22px;
  }
}

.catalog-filters--open {
  display: block;
}

.catalog-content {
  position: relative;
  min-height: 500px;

  .container {
    display: flex;
  }
}

.hide-filters {
  display: flex;
  align-items: center;
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;

  span {
    display: inline-flex;
    padding-left: 11px;
  }

  svg {
    margin-right: 5px;
    width: 12px;
    height: 12px;
  }
}

.catalog-filter {
  border-top: 1px solid #eee;

  // .catalog-filter__top
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 34px;
    cursor: pointer;
    user-select: none;
  }

  // .catalog-filter__title
  &__title {
    display: inline-block;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }

  // .catalog-filter__qualitty
  &__qualitty {
    min-width: 16px;
    transform: translateY(-2px);
  }

  // .catalog-filter__toggle
  &__toggle {
    position: relative;
    width: 12px;
    height: 12px;
    color: #aaa;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 2px;
      height: 100%;
      background-color: currentColor;
      transform: translateX(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transform: translateY(-50%);
    }
  }

  .catalog-filter {
    // .catalog-filter__bottom
    &__bottom {
      display: none;
      padding-bottom: 34px;
    }

    // .catalog-filter__items
    &__items {
      columns: 2;
    }

    // .catalog-filter__item
    &__item {
      margin-bottom: 14px;

      &:nth-child(5n) {
        margin-bottom: 0;
      }
    }
  }

  &:last-child {
    border-bottom: 1px solid #eee;
  }

  &--open {
    .catalog-filter__bottom {
      display: block;
    }

    .catalog-filter__toggle {
      &::before {
        display: none;
      }
    }
  }
}

.catalog-grid {
  padding-top: 32px;
  width: 987px;

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 70px;
    padding-top: 20px;

    @include md {
      justify-content: space-between;
    }
  }

  &__item {
    margin-bottom: 60px;

    @include md {
      margin-right: 0 !important;
      margin-bottom: 50px !important;
      width: 47% !important;

      &:nth-last-child(-n+2) {
        margin-bottom: 0 !important;
      }
    }
  }
}

[data-grid-columns="3"] {
  .catalog-grid__item {
    width: 31.3%;

    &:not(:nth-child(3n)) {
      margin-right: 3%;
    }

    &:nth-last-child(-n+3) {
      margin-bottom: 0;
    }
  }
}

[data-grid-columns="4"] {
  .catalog-grid__item {
    width: 22.7%;

    &:not(:nth-child(4n)) {
      margin-right: 3%;
    }

    &:nth-last-child(-n+4) {
      margin-bottom: 0;
    }
  }
}

[data-grid-columns="5"] {
  .catalog-grid__item {
    width: 17.6%;

    &:not(:nth-child(5n)) {
      margin-right: 3%;
    }

    &:nth-last-child(-n+5) {
      margin-bottom: 0;
    }
  }
}

.catalog-props {
  // .catalog-props__top
  &__top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 14px;

    select {
      display: none;
    }
  }
}

.catalog-columns {
  display: flex;
  align-items: flex-start;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;

  // .catalog-columns__text
  &__text {
    margin-right: 3px;
  }

  // .catalog-columns__list
  &__list {
    display: flex;
    align-items: center;
  }

  // .catalog-columns__item
  &__item {
    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  // .catalog-columns__btn
  &__btn {
    padding-right: 6px;
    padding-left: 5px;
    font-weight: 400;
    font-size: 12px;
    color: var(--color-gray);

    &:hover {
      color: var(--color-dark);
    }

    &--current {
      color: var(--color-dark);

      &::before {
        opacity: 1;
      }
    }
  }

  @include md {
    display: none;
  }
}

.catalog-choice {
  display: none;
  font-size: 0;

  // .catalog-choice__item
  &__item {
    margin-right: 7px;
    margin-bottom: 7px;
    border-radius: 12px;
    padding: 4px 19px;
    font-size: 13px;
    color: var(--color-tundora);
    background-color: #eee;
    transition: background-color 0.3s, color 0.3s;

    svg {
      width: 8px;
      height: 9px;
      fill: currentColor;
      pointer-events: none;
    }

    &:hover {
      color: var(--color-white);
      background-color: var(--color-dark);
      transition: background-color 0.3s, color 0.3s;
    }
  }

  // .catalog-choice__clear
  &__clear {
    position: relative;
    padding-bottom: 4px;
    font-weight: 500;
    font-size: 13px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      opacity: 1;
      transition: opacity 0.3s ease;
    }
  }
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;

  // .pagination__item
  &__item {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  // .pagination__link
  &__link {
    display: inline-flex;
    align-items: center;
    border: 2px solid #eee;
    padding: 0 16px;
    height: 40px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    transition: ease color 0.3s, background-color 0.3s;

    svg {
      margin-left: 3px;
      width: 7px;
      height: 16px;
      fill: currentColor;
    }

    &--current {
      color: var(--color-white);
      background-color: var(--color-dark);
    }

    @media (min-width: 992px) {
      &:hover {
        color: var(--color-white);
        background-color: var(--color-dark);
      }
    }
  }
}

.catalog-mobile-filters {
  display: none;

  svg {
    margin-right: 5px;
    width: 20px;
    height: 20px;
  }

  @include mobile {
    display: inline-flex;
    align-items: center;
  }
}
