.hero-catalog {
  padding-top: 65px;

  // .hero-catalog__slider
  &__slider {
    position: relative;
    overflow: hidden;
    width: 100%;
    background-color: #f8f8f8;
  }

  .swiper-slide {
    overflow: hidden;
  }

  @include mobile {
    padding-top: 50px;
  }
}

.catalog-slide {
  padding: 108px;
  background-image: url("../img/str-2/background.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  // .catalog-slide__title
  &__title {
    margin-bottom: 20px;
    max-width: 385px;
    font-weight: 700;
    font-size: 36px;
  }

  // .catalog-slide__descr
  &__descr {
    margin: 0;
    margin-bottom: 40px;
    max-width: 390px;
    font-size: 15px;
    line-height: 1.7;
    color: var(--color-tundora);
  }

  // .catalog-slide__btns
  &__btns {
    display: flex;
    align-items: center;

    @include xs {
      display: block;
      padding-bottom: 70px;
    }
  }

  // .catalog-slide__link
  &__link {
    display: inline-block;
    padding: 17px 44px;
    font-size: 13px;

    &:nth-child(1) {
      margin-right: 12px;

      @include xs {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
  }

  // .catalog-slide__link--dark
  &__link--dark {
    border: 1px solid var(--color-dark);
    color: var(--color-white);
    background-color: var(--color-dark);
    transition: ease background-color 0.3s, ease color 0.3s;

    &:hover {
      color: var(--color-dark);
      background-color: transparent;
      transition: ease background-color 0.3s, ease color 0.3s;
    }
  }

  // .catalog-slide__light
  &__link--light {
    border: 1px solid var(--color-dark);
    color: var(--color-dark);
    background-color: transparent;
    transition: ease background-color 0.3s, ease color 0.3s;

    &:hover {
      color: var(--color-white);
      background-color: var(--color-dark);
      transition: ease background-color 0.3s, ease color 0.3s;
    }
  }

  @include md {
    background-position: center bottom;
    background-size: 100%;
  }

  @include mobile {
    padding: 80px;
    background-image: none;
  }

  @include xs {
    padding: 20px;
  }
}

.hero-prev-btn,
.hero-next-btn {
  position: absolute;
  left: 30px;
  top: 50%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-dark);
  width: 48px;
  height: 48px;
  color: var(--color-dark);
  transform: translateY(-50%);
  transition: background-color 0.3s;

  svg {
    width: 40px;
    height: 20px;
    fill: currentColor;
  }

  @include mobile {
    left: 10px;
  }

  @include xs {
    display: none;
  }

  @media (min-width: 992px) {
    &:hover {
      background-color: var(--color-dark);
      transition: background-color 0.3s;

      svg {
        fill: var(--color-white);
      }
    }
  }
}

.hero-next-btn {
  left: auto;
  right: 30px;

  @include mobile {
    right: 10px;
  }
}
