.card {
  padding-top: 69px;

  // .card__info-bottom
  &__info-bottom {
    margin-top: 50px;
  }

  &__content-bottom {
    padding-top: 70px;
  }

  // .card__content-related
  &__content-related {
    padding-top: 82px;
  }
}

.card-content {
  padding-top: 20px;
}

.card-top {
  .container {
    display: flex;

    @include mobile {
      flex-direction: column;
    }
  }

  &__left {
    width: 57.5%;

    @include mobile {
      width: 100%;
    }
  }

  &__right {
    padding-top: 57px;
    padding-left: 10.7%;
    width: calc(100% - 57.5%);

    @include md {
      padding-top: 30px;
      padding-left: 4%;
    }

    @include mobile {
      padding-left: 0;
      width: 100%;
    }
  }
}

.card-info {
  &__price {
    margin-bottom: 23px;
    font-weight: 700;
    font-size: 24px;
    color: var(--color-dark);
  }

  &__price-old {
    margin-left: 20px;
    font-weight: 500;
    font-size: 16px;
    color: #959595;
  }

  // .card-info__prop
  &__prop {
    margin-bottom: 15px;
    padding-left: 20px;
  }

  // .card-info_descr
  &_descr {
    margin-bottom: 53px;
    font-size: 13px;
    line-height: 20px;
    color: var(--color-tundora);

    @include mobile {
      margin-bottom: 20px;
    }
  }

  // .card-info__title
  &__title {
    margin-bottom: 14px;
    font-weight: 500;
    font-size: 24px;
    color: var(--color-dark);
  }

  &__stepper {
    margin-bottom: 10px;
  }

  // .card-info__rate
  &__rate {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    font-size: 13px;
    color: var(--color-gray);

    span {
      display: inline-flex;
      margin-left: 7px;
    }

    svg {
      width: 12px;
      height: 12px;
    }

    &--current {
      fill: #0f0;
    }

    &--svg {
      fill: var(--color-gray);
    }
  }

  &__first-select {
    margin-bottom: 28px;
  }

  &__second-select {
    margin-bottom: 55px;

    @include mobile {
      margin-bottom: 30px;
    }
  }

  // .card-info__btn
  &__btn {
    margin-bottom: 10px;
    width: 100%;
    height: 58px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    background-color: #f2f2f2;
    transition: ease color 0.3s, background-color 0.3s;

    // .card-info__btn--tocard
    &--tocard {
      color: var(--color-white);
      background-color: var(--color-dark);

      @media (min-width: 992px) {
        &:hover {
          color: var(--color-dark);
          background-color: #f2f2f2;
          transition: ease color 0.3s, background-color 0.3s;
        }
      }
    }

    // .card-info__btn--towishlist
    &--towishlist {
      @media (min-width: 992px) {
        &:hover {
          color: var(--color-white);
          background-color: var(--color-dark);
          transition: ease color 0.3s, background-color 0.3s;
        }
      }
    }
  }
}

.color-select {
  &__selected {
    margin-bottom: 18px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);

    span {
      margin-left: 5px;
      color: var(--color-dark);
    }
  }

  // .color-select__list
  &__list {
    display: flex;
    align-items: center;
    padding-left: 4px;
  }

  // .color-select__item
  &__item {
    &:not(:last-child) {
      margin-right: 13px;
    }
  }

  // .color-select__btn
  &__btn {
    position: relative;
    border-radius: 100%;
    width: 24px;
    height: 24px;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      border: 1px solid #ccc;
      border-radius: 100%;
      width: 32px;
      height: 32px;
      transform: translate(-50%, -50%);
      transition: ease border 0.3s;
    }

    &--active {
      &::before {
        border: 1px solid var(--color-dark);
        transition: ease border 0.3s;
      }
    }

    @media (min-width: 992px) {
      &:hover {
        &::before {
          border: 1px solid var(--color-dark);
          transition: ease border 0.3s;
        }
      }
    }
  }
}

.size-select {
  // .size-select__selected
  &__selected {
    margin-bottom: 13px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);

    span {
      margin-left: 5px;
      color: var(--color-dark);
    }
  }

  // .size-select__list
  &__list {
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    @include lg {
      flex-wrap: wrap;
    }
  }

  // .size-select__item
  &__item {
    &:not(:last-child) {
      margin-right: 5px;
    }

    @include lg {
      margin-bottom: 5px;
    }
  }

  // .size-select__btn
  &__btn {
    position: relative;
    border: 1px solid #ccc;
    width: 42px;
    height: 32px;
    font-weight: 500;
    font-size: 13px;
    color: var(--color-gray);
    transition: ease border 0.3s, color 0.3s;

    @media (min-width: 992px) {
      &:hover {
        border: 1px solid var(--color-dark);
        transition: ease border 0.3s, color 0.3s;
      }
    }
  }

  // .size-select__btn--active
  &__btn--active {
    border: 1px solid var(--color-dark);
    color: var(--color-dark);
    transition: ease border 0.3s, color 0.3s;
  }

  &__clear {
    display: none;
    align-items: center;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: #d24242;

    svg {
      margin-right: 4px;
      width: 8px;
      height: 8px;
      fill: currentColor;
    }
  }
}

.stepper {
  display: flex;
  border: 1px solid #ccc;
  height: 58px;

  // .stepper__btn
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 100%;

    svg {
      width: 12px;
      height: 12px;
      fill: var(--color-tundora);
      transition: ease fill 0.3s;
    }

    @media (min-width: 992px) {
      &:hover {
        svg {
          fill: var(--color-dark);
          transition: ease fill 0.3s;
        }
      }

      &--disabled {
        opacity: 0.4;
        pointer-events: none;
      }
    }
  }

  // .stepper__input
  &__input {
    flex: 1 1 auto;
    border: none;
    height: 100%;
    font-weight: 700;
    font-size: 13px;
    text-align: center;
    text-transform: uppercase;
    color: var(--color-gray);
  }
}

.card-bottom {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;

  span {
    color: var(--color-gray);
  }

  // .card-bottom__item
  &__item {
    a {
      @media (min-width: 992px) {
        &:hover {
          text-decoration: underline;
          transition: ease text-decoration 0.3s;
        }
      }
    }

    @include lg {
      margin-bottom: 10px;
    }
  }

  // .card-bottom__social
  &__social {
    display: flex;
    align-items: center;
    margin-top: 17px;

    span {
      margin-right: 12px;
    }

    li {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }

    @include md {
      width: 100%;
    }
  }
}

.card-description {
  display: flex;
  align-items: flex-start;

  // .card-description__left
  &__left {
    padding-top: 54px;
    width: 16%;

    @include md {
      width: 45%;
    }

    @include xs {
      width: 80%;
    }
  }

  .simplebar-track.simplebar-vertical {
    width: 3px;
    background-color: #ccc;
  }

  .simplebar-scrollbar::before {
    left: 0;
    right: 0;
    border-radius: 0;
    width: 3px;
    height: 100%;
    background-color: var(--color-dark);
    opacity: 1;
  }

  // .card-description__navigation
  &__navigation {
    width: 100%;
    max-height: 210px;
  }

  // .card-description__item
  &__item {
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  // .card-description__link
  &__link {
    font-weight: 700;
    font-size: 15px;
    color: var(--color-gray);
    transition: ease color 0.3s;

    &--active {
      color: var(--color-dark);
      pointer-events: none;
    }

    @media (min-width: 992px) {
      &:hover {
        color: var(--color-dark);
        transition: ease color 0.3s;
      }
    }
  }

  // .card-description__right
  &__right {
    padding-top: 22px;
    padding-right: 155px / 1920px * 100%;
    padding-left: 183px / 1920px * 100%;
    width: 84%;

    @include md {
      padding-top: 20px;
      padding-right: 2.07292%;
      padding-left: 2.53125%;
      width: 84%;
    }

    @include xs {
      padding-top: 20px;
      padding-right: 2%;
      padding-left: 0;
      width: 100%;
    }
  }

  // .card-description__content
  &__content {
    display: none;

    p {
      font-weight: 500;
      font-size: 13px;
      line-height: 26px;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &--active {
      display: block;
    }
  }

  @include xs {
    flex-direction: column;
  }
}

.card-related {
  // .card-related__title
  &__title {
    margin-bottom: 42px;
    font-weight: 500;
    font-size: 24px;
  }

  // .card-related__slider
  &__slider {
    position: relative;
    overflow: hidden;
    padding-bottom: 85px;
  }
}

.related-pag {
  left: 0;
  bottom: 5px;
  text-align: left;

  .swiper-pagination-bullet {
    margin: 0 10px !important;
    width: 6px;
    height: 6px;
    background-color: #ccc;
    opacity: 0.5;
  }

  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--color-dark);
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -1;
      border-radius: 100%;
      width: 20px;
      height: 20px;
      background-color: #ccc;
      transform: translate(-50%, -50%);
    }
  }
}
