.main-products {
  // .main-products__container
  &__container {
    padding-top: 55px;
    padding-bottom: 55px;

    @include md {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  // .main-products__nav
  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 38px;
  }

  // .main-products__item
  &__item {
    margin: 0 18px;
  }

  // .main-products__btn
  &__btn {
    font-weight: 500;
    font-size: 24px;

    @include md {
      font-size: 20px;
    }
  }

  // .main-products__btn-center
  &__btn-center {
    text-align: center;
  }

  // .main-products__more
  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin-top: 95px;
    font-weight: 500;
    font-size: 13px;
    color: var(--color-gray);

    span {
      display: inline-block;
      margin-bottom: 12px;
    }

    svg {
      width: 12px;
      height: 21px;
      fill: var(--color-dark);
      transition: ease transform 0.3s;
    }

    @include md {
      margin-top: 50px;
    }

    @media (min-width: 992px) {
      &:hover {
        svg {
          transform: translateY(3px);
          transition: ease transform 0.3s;
        }
      }
    }
  }
}
